import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Button } from 'antd';
import { CartHeadAction, TableData } from 'components/common';
import { numberToMoney } from 'utils/formatUtil';
import { getQueryParams } from 'utils/urlUtil';
import { getReceipts, exportReceiptAndExpenses } from 'services/receiptsAndExpensesStatistic';
import FormSearchStudent from './FormSearchStudent';
import './style.scss';

class ReceiptsAndExpensesStatisticPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
    printing: false,
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  getReceipts = async paramSearch => {
    const res = await getReceipts(paramSearch);
    let count = 0;
    res.data.items.forEach(item => {
      item.index = count + 1;
      count += 1;
    });
    return res;
  };

  handleChangeQueryParams = () => {
    const paramSearch = getQueryParams();
    this.setState({ paramSearch });
  };

  exportReceiptAndExpenses = async paramSearch => {
    this.setState({ printing: true });
    await exportReceiptAndExpenses(paramSearch);
    this.setState({ printing: false });
  };

  enablePrintButton = () => {
    const { paramSearch } = this.state;
    return !!paramSearch.trainTypeId && !!paramSearch.courseId && !!paramSearch.yearEduId;
  };

  render() {
    const { reload, paramSearch, printing } = this.state;
    const columns = [
      {
        title: 'STT',
        dataIndex: 'index',
        key: 'index',
        align: 'center',
      },
      {
        title: 'Khoản thu',
        dataIndex: 'receivable',
        key: 'receivable',
        align: 'left',
      },
      {
        title: 'Số tiền phải thu',
        dataIndex: 'amount',
        key: 'amount',
        align: 'right',
        render: value => numberToMoney(value),
      },
      {
        title: 'Số tiền đã thu',
        dataIndex: 'revenuedAmount',
        key: 'revenuedAmount',
        align: 'right',
        render: value => numberToMoney(value),
      },
      {
        title: 'Số tiền đã chi',
        dataIndex: 'expendituredAmount',
        key: 'expendituredAmount',
        align: 'right',
        render: value => numberToMoney(value),
      },
      {
        title: 'Số tiền còn lại',
        dataIndex: 'remainingAmount',
        key: 'remainingAmount',
        align: 'right',
        render: value => numberToMoney(value),
      },
    ];

    return (
      <Card title="Báo cáo tổng hợp thu/chi">
        <CartHeadAction>
          <div className="btn-group">
            <Button
              className="btnAction btnPrint"
              icon="printer"
              size="large"
              disabled={!this.enablePrintButton()}
              onClick={() => this.exportReceiptAndExpenses(paramSearch)}
              loading={printing}
            >
              In
            </Button>
          </div>
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <FormSearchStudent />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              service={this.getReceipts}
              paramSearch={paramSearch}
              allowGetData={
                !!paramSearch.trainTypeId && !!paramSearch.courseId && !!paramSearch.yearEduId
              }
              columns={columns}
              rowKey="index"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ReceiptsAndExpensesStatisticPage />
  </Page>
);



