import { request, requestDownload } from 'utils';

const prefix = '/api/finance';

export const getReceipts = paramSearch =>
  request({
    prefix,
    url: '/Receipt/BrowseSummarizedRevenueAndExpenditure',
    params: { ...paramSearch },
    method: 'GET',
  });

export const getBillById = id => {
  console.log(id);
  return {
    data: {
      index: '1',
      name: 'Hóa đơn tài chính',
      templateCode: '01BLP2-001',
      code: 'MT-13T',
      financialYear: 2018,
      startRemainAmountFrom: 1,
      startRemainAmountTo: 100,
      startProduceAmountFrom: 201,
      startProduceAmountTo: 300,
      deleteAmount: 0,
      deletedItems: '',
      lostAmount: 0,
      lostItems: '',
    },
  };
};

export const exportReceiptAndExpenses = paramSearch =>
  requestDownload({
    defaultFileName: 'Báo cáo thu chi.pdf',
    prefix,
    url: '/Receipt/ExportSummarizedRevenueAndExpenditure',
    method: 'POST',
    params: {
      ...paramSearch,
    },
  });



